<template>
  <div class="days-zone">
    <List
      v-model="allDays"
      @itemClick="itemClick"
      @delete="delDay"
      :use-theme="useTheme"
    />
    <Footer :use-theme.sync="useTheme" @showAdd="showAddDay = true" />
    <DayInfo :day="currentDay" :visible.sync="showDayInfo" />
    <AddDay :visible.sync="showAddDay" @add="addDay" />
  </div>
</template>
<script>
import List from "./modules/List";
import Footer from "./modules/Footer";
import DayInfo from "./modules/DayInfo";
import AddDay from "./modules/AddDay";
import daysOperate from "./days-opt";
export default {
  name: "DaysList",
  components: { List, Footer, DayInfo, AddDay },
  data() {
    return {
      allDays: [],
      showDayInfo: false,
      currentDay: {},
      useTheme: this.getTheme(),
      showAddDay: false,
    };
  },
  mounted() {
    this.getDays();
    // this.useTheme = this.getTheme();
  },
  watch: {
    useTheme(val) {
      this.setTheme(val);
    },
  },
  methods: {
    ...daysOperate,
    itemClick(val) {
      this.currentDay = val;
      this.showDayInfo = true;
    },
    getDays() {
      this.allDays = this.findDays();
    },
    addDay(val) {
      this.insertDay(val);
      this.getDays();
    },
    delDay(val) {
      this.removeDay(val);
      this.getDays();
    },
  },
};
</script>
<style scoped>
.days-zone {
  width: 100%;
  height: 100%;
  background-color: var(--list-bg-color);
  border-radius: var(--radius) var(--radius) 0 0;
  padding-top: var(--radius);
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
}
</style>
